<template>
    <div class="wrapper">
      <div class="title">v2.0 by -3-</div>
      <div class="count">
        {{ rightCount }}/{{ totalCount }}({{ rightRate }}%)
      </div>
      <div class="ope">
        <el-input v-model="input" placeholder="" :disabled="isWin" @keyup.enter.native="guess()"></el-input>
        <el-button type="primary" class="btn" :disabled="input.length !== 1" @click="guess()"></el-button>
        <el-button type="warning" class="btn" :disabled="isWin" @click="giveUp()"></el-button>
        <el-button class="btn" @click="dialogMode = 2"></el-button>
      </div>
      <div class="content">
        <div v-for="(p, index) in ans" :key="index" class="paragraph">
          <div v-for="(c, index2) in p" :key="index2" class="block" @click="clickBlock(index, index2)"
          :class="{
            white: c.status === 0,
            black: c.status === 1,
            green: c.status === 2,
            out: c.status === 3,
            last: c.character === lastC && c.status === 2
          }">
            <span v-if="c.status !== 1">{{ c.character }}</span>
          </div>
        </div>
      </div>
      <div class="history">
        <div class="top"></div>
        <div class="bot">
          <div v-for="(h, index) in his" :key="index" class="block" @click="clickHis(index)" 
          :class="{
            right: h.status,
            wrong: !h.status,
            last: h.character === lastC && h.status
          }">{{ h.character }}</div>
        </div>
      </div>

      <el-dialog title="" center :visible.sync="dialog1" width="300px" :show-close="false">
        <div class="out">
          <el-button type="success" :disabled="isAIThinking" @click="ai()">
            <span v-if="isAIThinking"><i class="el-icon-loading"></i>10</span>
            <span v-else>AI</span>
          </el-button>
          <el-button type="info" @click="dialogMode=2"></el-button>
        </div>
      </el-dialog>
      <el-dialog title="" center :visible.sync="dialog2" width="75%" @close="dialogMode = (isAIThinking ? 0 : 1)">
        <el-input type="textarea" :rows="10" placeholder="" v-model="textarea"></el-input>
        <span slot="footer" class="">
          <el-button type="primary" :disabled="textarea.length===0" @click="customize()"></el-button>
        </span>
      </el-dialog>
    </div>
  </template>
  
  <style scoped lang="scss">
    .wrapper{
      max-width: 800px;
      margin: 0 auto;
      padding: 0 20px;
      .title{
        margin-top: 20px;
        margin-bottom: 20px;
        font-size: 24px;
        text-align: center;
      }
      .count{
        text-align: center;
      }
      .ope{
        margin-top: 20px;
        background-color: white;
        padding: 10px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        position: sticky;
        top: 0;
        .btn{
          width: 100px;
          margin-left: 10px;
        }
      }
      .content{
        margin-top: 10px;
        .paragraph{
          margin-bottom: 10px;
          display: flex;
          flex-flow: row wrap;
          .block{
            width: 24px;
            height: 24px;
            line-height: 24px;
            font-size: 20px;
            text-align: center;
            margin: 0 5px 5px 0;
            border: 1px solid rgba(255, 255, 255, 0);
            cursor: default;
          }
          .white{

          }
          .black{
            background-color: #333;
          }
          .green{
            background-color: rgb(51,103,209);
            color: white;
          }
          .out{
            border-color: #ccc;
          }
          .last{
            background-color: #EE82EE;
            font-weight: bold;
          }
        }
      }
      .history{
        margin: 20px 0;
        border-top: 1px solid #ccc;
        .top{
          color: #666;
          font-size: 16px;
          margin: 10px 0;
        }
        .bot{
          color: white;
          display: flex;
          flex-flow: row wrap;
          .block{
            width: 24px;
            height: 24px;
            line-height: 24px;
            font-size: 20px;
            text-align: center;
            margin: 0 5px 5px 0;
            cursor: default;
          }
          .right{background-color: rgb(51,103,209);}
          .wrong{background-color: rgb(184, 184, 184);}
          .last{
            background-color: #EE82EE;
            font-weight: bold;
          }
        }
      }

      .out{
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
      }
    }
  </style>
  
  <script>
    const { Configuration, OpenAIApi } = require("openai");
    import {Base64} from "js-base64"

    export default {
      name: "guessBK",
      data(){
        return{
          punctuationMarks: `,.\\/:;\`~'"()<>!?+-_%`,
          rightCount: 0,
          totalCount: 0,
          ans: [],
          his: [],
          input: "",
          isWin: false,
          dialogMode: 0,
          textarea: "",
          isAIThinking: false,
          lastC: ""
        }
      },
      computed: {
        dialog1(){return this.dialogMode === 1},
        dialog2(){return this.dialogMode === 2},
        rightRate(){
          if(this.rightCount === 0 && this.totalCount === 0) return 0;
          return Math.floor(this.rightCount / this.totalCount * 100 * 100) / 100;
        }
      },
      mounted(){
        if(this.$route.query.k){
          let code = this.$route.query.k;
          let decode = Base64.decode(decodeURIComponent(code));
          this.generate(decode);
          this.isAIThinking = true;
        } else {
          this.dialogMode = 1;
        }
      },
      methods: {
        getRandomBKCategory(){
          let arr = [
            //xxx
            "", "", "", "",//
            "", "", "", //
            "", "", "", "", //
            "", "", "", //
            "", "", "", "", //
            "", "", "", //
            "", //
            "", "", "", //
            "", //
            "", "", //
            "", //
            "", "Bup", "", //
          ];
          return arr[Math.floor(Math.random() * arr.length)];
        },
        generate(article){
          let paragraphs = article.split("##").map(e => e.trim());
          for(let p of paragraphs){
            let temp = [];
            for(let c of p){
              temp.push({
                character: c,
                status: this.punctuationMarks.includes(c) ? 0 : 1 //0 1 2 3 4
              });
            }
            this.ans.push(temp);
          }
        },
        async ai(){
          const configuration = new Configuration({
            basePath: 'https://api.deepseek.com',
            apiKey: "sk-30653396b0084fa18136b6a2f8da6f2e",
          });
          delete configuration.baseOptions.headers['User-Agent'];
          const openai = new OpenAIApi(configuration);
          this.isAIThinking = true;
          const completion = await openai.createChatCompletion({
            messages: [{ role: "user", content: "" + this.getRandomBKCategory() + "23200250" }],
            model: "deepseek-chat",
            temperature: 2
          });
          let rawAns = completion.data.choices[0].message.content;
          rawAns = rawAns.replaceAll("**", "").replaceAll("\n\n", "\n").replaceAll("\n", "##");
          this.generate(rawAns);
          this.dialogMode = 0;
        },
        guess(){
          if(this.input.length !== 1) {
            this.$message.warning('');
            return;
          }
          if(this.punctuationMarks.includes(this.input)) {
            this.$message.waring('');
            return;
          }

          this.lastC = this.input;

          //
          if(this.his.some(h => h.character === this.input)){
            this.$message('');
            this.input = "";
            return;
          }

          //
          let flag = false;
          for(let p of this.ans){
            for(let c of p){
              if(c.character === this.input){
                c.status = 2;
                flag = true;
              }
            }
          }

          if(flag){
            this.rightCount++;
          } else {
            this.$message.error('');
          }
          this.totalCount++;

          //
          this.his.push({
            character: this.input,
            status: flag
          });

          //
          this.input = "";

          //
          this.judgeWin();
        },
        judgeWin(){
          if(this.ans[0].every(c => c.status === 2)){
            for(let p of this.ans){
              for(let c of p){
                if(c.status === 1){
                  c.status = 3;
                }
              }
            }
            this.isWin = true;
            this.lastC = "";
            this.$message.success('');
          }
        },
        customize(){
          let temp = this.textarea;
          temp = temp.replaceAll(/\n/g, "##");
          let fullurl = window.location.href;
          if(fullurl.includes("?")){
            fullurl = fullurl.substring(0, fullurl.indexOf("?"));
          }
          let url = fullurl + "?k=" + encodeURIComponent(Base64.encode(temp));
          navigator.clipboard.writeText(url);
          console.log(url);
          this.$message.success('');
        },
        giveUp(){
          if (confirm("")) {
            for(let p of this.ans){
              for(let c of p){
                if(c.status === 1){
                  c.status = 3;
                }
              }
            }
            this.isWin = true;
            this.lastC = "";
          }
        },
        clickBlock(index, index2){
          if(this.ans[index][index2].status === 2 && !this.isWin){
            this.lastC = this.ans[index][index2].character;
          }
        },
        clickHis(index){
          if(this.his[index].status && !this.isWin){
            this.lastC = this.his[index].character;
          }
        }
      }
    }
  </script>
  


